import '../styles/globals.css'
// Asegúrate de importar tu Layout si lo tienes en otra carpeta, 
// por ejemplo: import Layout from '../components/Layout'
// Si el Layout está definido en este mismo archivo o es global, omite el import.

function MyApp({ Component, pageProps }) {
  return (
    // Si usas un componente <Layout>, déjalo aquí. Si no, borra las etiquetas <Layout>
    <Layout>
      {/* CAMBIO: Usamos <div> porque <p> no puede contener otros bloques de texto */}
      <div className="text-xl md:text-2xl text-gray-800 font-medium">
        <Component {...pageProps} />
      </div>
    </Layout>
  )
}

export default MyApp